import React from "react";

import SetInitialLemmata from './SetInitialLemmata';
import LemmataList from './LemmataList';


////////////////////////////////////////////////////////////////////////////////
// FAKE DATA LOADING STUFF
import DATA from '../data/sample-data.json';
function getLanguageList(lemmata) {
  
  let languages = [];
  lemmata.map(lemma => {
    if (!languages.includes(lemma.language))
      languages.push(lemma.language);
  });
  console.log(languages);
  return [ 'Egyptian', 'Greek' ];
}

function loadLemmata() {
  let lemmata = null;
  lemmata = JSON.parse(localStorage.getItem('lemmata'));
  if (!lemmata) {
    localStorage.setItem("lemmata", JSON.stringify(DATA.lemmata));
    lemmata = DATA.lemmata;
  }
  return lemmata || [];
}
////////////////////////////////////////////////////////////////////////////////



function LemmataContainer(props) {
  const [lemmata, setLemmata] = React.useState(getLemmata());
  const [languages, setLanguages] = React.useState(getLanguageList(lemmata));
  
  function getLemmata() {
    // const savedLemmata = JSON.parse(localStorage.getItem('lemmata'));
    // return savedLemmata || [];
    return loadLemmata();
  }
  
  
  
  return (
    <>
      <SetInitialLemmata lemmata={lemmata} />
      <LemmataList lemmata={lemmata} />
    </>
  )
}

export default LemmataContainer;